define('modules/app/directives/ladda',['./module', 'spin', 'ladda', 'lodash'], function (directives, spin, Ladda, _) {
    'use strict';
    directives.directive('ladda', ['$timeout', '$compile', function ($timeout, $compile) {
        return {
            restrict: 'A',
            priority: -1,
            scope: {
                ladda: '&',
                numericalProgress: '&laddaNumericalProgress'
            },
            link: function (scope, element, attrs) {
                // todo: Refactor
                const numericalProgressTemplate = `<span class="ladda-numerical-progress">{{numericalProgress()}}%</span>`;
                let laddaOption = {};

                function initNumericalProgress() {
                    let laddaSpinner = element.find('.ladda-spinner');
                    let progressElement = $(numericalProgressTemplate).prependTo(laddaSpinner);
                    return $compile(progressElement)(scope);
                }

                element.addClass('ladda-button');
                if (!_.isUndefined(attrs.laddaNumericalProgress)) {
                    $timeout(initNumericalProgress);
                }
                if (_.isUndefined(element.attr('data-style'))) {
                    element.attr('data-style', laddaOption.style || 'zoom-in');
                }
                if (_.isUndefined(element.attr('data-spinner-color')) && attrs.laddaSpinnerColor) {
                    element.attr('data-spinner-color', attrs.laddaSpinnerColor);
                }

                // ladda breaks childNode's event property.
                // because ladda use innerHTML instead of append node
                if (!element[0].querySelector('.ladda-label')) {
                    var labelWrapper = document.createElement('span');
                    labelWrapper.className = 'ladda-label';
                    angular.element(labelWrapper).append(element.contents());
                    element.append(labelWrapper);
                }

                // create ladda button
                var ladda = Ladda.create(element[0]);

                // add watch!
                scope.$watch(scope.ladda, function (loading) {
                    if (!loading && !angular.isNumber(loading)) {
                        ladda.stop();
                        // When the button also have the ng-disabled directive it needs to be
                        // re-evaluated since the disabled attribute is removed by the 'stop' method.
                        if (attrs.ngDisabled) {
                            element.attr('disabled', scope.$eval(attrs.ngDisabled));
                        }
                        return;
                    }
                    if (!ladda.isLoading()) {
                        ladda.start();
                    }
                    if (angular.isNumber(loading)) {
                        ladda.setProgress(loading);
                    }
                });
            }
        };
    }]);
});
define('modules/app/directives/ng-enter',['./module'], function (directives) {
    'use strict';
    directives.directive('ngEnter', [function () {
        return function(scope, element, attrs) {
            element.bind("keydown keypress", function(event) {
                if(event.which === 13 && !event.isPropagationStopped()) {
                    scope.$apply(function(){
                        scope.$eval(attrs.ngEnter);
                    });

                    event.preventDefault();
                }
            });
        };
    }])
});

define('modules/app/directives/page-loader',['./module', 'angular', 'lodash'], function (directives, angular, _) {
    'use strict';
    directives.directive('pageLoader', ['$rootScope', function ($rootScope) {
        return {
            restrict: 'A',
            templateUrl: 'static/templates/app/page-loader.html',
            scope: {},
            link: function ($scope, element, attrs) {
                $scope.stateChange = true;
                $scope.showFullBackdrop = true;
                $scope.showCancelRequestButton = false;
                $scope.innerText = undefined;
                $rootScope.$on('$stateChangeStart', function(event, toState, toParams, fromState, fromParams) {
                    $scope.showCancelRequestButton = false;
                    $scope.stateChange = true;
                    $scope.showFullBackdrop = true;
                });
                $rootScope.$on('$stateChangeSuccess', function(event, toState, toParams, fromState, fromParams) {
                    if(!toState.skipDefaultStateChangeBehavior) {
                        $scope.stateChange = false;
                        $scope.showFullBackdrop = false;
                    }
                });
                $rootScope.$on('togglePageLoader-' + attrs.id, function (e, options) {
                    options = _.merge({toggle: true, backdrop: true}, options);
                    $scope.showCancelRequestButton = false;
                    $scope.stateChange = options.toggle;
                    $scope.showFullBackdrop = options.backdrop;
                    $scope.innerText =  options.innerText;
                });
                $rootScope.$on('togglePageLoaderCancelButton-' + attrs.id, function(e, cancelCallback) {
                    $scope.showCancelRequestButton = true;
                    $scope.cancelRequest = cancelCallback;
                    $scope.innerText = undefined;
                });
            }
        }
    }])
});

define('modules/app/directives/popover-close-button',['./module'], function (directives) {
    'use strict';
    directives.directive('popoverCloseButton', function($timeout) {
        return {
            scope: true,
            link: function(scope, element) {
                scope.togglePopover = () => {
                    $timeout(() => {
                        element.triggerHandler('close');
                    });
                };

                return element.on('click', scope.togglePopover);
            }
        };
    });
});

define('modules/app/directives/search-bar',['./module', 'lodash'], function (directives, _) {
    'use strict';
    directives.directive('searchBar', ['$state', 'SearchBarService', '$rootScope', 'SourceService',
        function ($state, SearchBarService, $rootScope, SourceService) {
        return {
            restrict: 'E',
            templateUrl: 'static/templates/app/search-bar.html',
            transclude: {
                'dropdownOptions': '?dropdownOptions'
            },
            link: function ($scope, $elm) {

                $scope.searchInput = { keepClosed: false };
                $scope.currentSuggestions = {};
                $scope.previousSearch = null;

                $scope.$on('update-ingesting-value', function (e, source) {
                    if (source) {
                        const suggestion = _.find(_.get($scope.currentSuggestions, '$$state.value'), {id: source.id});
                        if (suggestion) {
                            suggestion.ingestingProgress = source.ingestingProgress;
                        }
                    }
                });

                $scope.isLandingPage = function() {
                    return !!~$state.current.name.indexOf('landing');
                };

                $scope.isVisualization = function() {
                    return !!~$state.current.name.indexOf('visualize');
                };

                SearchBarService.resetSearch = function(search){
                    $scope.searchInput.value = search;
                    $scope.searchInputChange($scope.searchInput.value);
                };

                SearchBarService.setSearch = function(search){
                    $scope.previousSearch = search;
                    $scope.searchInput.value = search;
                };

                $scope.searchInputChange = function (val, force) {
                    if((val !== null && val !== undefined && val !== '') || $scope.previousSearch) {
                        if (SearchBarService.callback) {
                            const callbackResult = SearchBarService.callback(val, force);
                            if(callbackResult !== -1) {
                                $scope.previousSearch = val;
                            }
                        }
                        $scope.searchInput.noSearchResults = false;
                    }
                };

                $scope.getSuggestions = function(val){
                    const suggestions = SearchBarService.suggestions ? SearchBarService.suggestions(val) : [];

                    suggestions.then((result) => {
                        _.each(result, file => {
                            const loadedFile = SourceService.get(file.id);
                            file.ingestingProgress = _.get(loadedFile, 'ingestingProgress');
                        });
                    });

                    $scope.currentSuggestions = suggestions;
                    return suggestions;
                };

                $scope.onSuggestionSelected = function($item, $model, $label, $event){
                    if(SearchBarService.suggestionSelected){
                        $event.stopPropagation();
                        SearchBarService.suggestionSelected($item, $model, $label, $event);
                    }
                };

                function _searchInputChange (val, force){
                    $scope.$apply(() => $scope.searchInputChange(val, force));
                }

                $scope.delayedSearchInputChange = _.debounce(_searchInputChange, 600);

            }
        }
    }]);
});

define('modules/app/directives/user-menu',['./module', 'lodash', 'common'], function (directives, _, cc) {
    'use strict';
    directives.directive('userMenu', ['$state', 'User', 'FileUploader', '$http', 'WSocket',
        function ($state, User, FileUploader, $http, WSocket) {
            return {
                restrict: 'E',
                templateUrl: 'static/templates/include/user-menu.html',
                compile: function (el, attrs) {
                    return {
                        pre: function ($scope, $elm, $attr) {
                            $scope.avatarUploader = new FileUploader({
                                url: '/api/user/upload_avatar',
                                filters: [{
                                    name: 'onlyImages',
                                    fn: function (item) {
                                        return _.startsWith(item.type, 'image');
                                    }
                                }]
                            });
                        },
                        post: function ($scope, $elm, $attr) {
                            $scope.signedIn = User.isSignedIn;
                            $scope.user = User.getCurrent;
                            $scope.getUserInitials = User.getUserInitials;

                            $scope.signOut = function () {
                                WSocket.unsubscribe('/user/event/sign_out');
                                User.signOut().then(function () {
                                    $state.go('auth.login');
                                });
                            };

                            function changeAvatar(url) {
                                $http.post(`/api/user/change-avatar`, {url})
                                    .then(() => {
                                        User.reinitialize();
                                        cc.notify({message: "User avatar successfully updated.", icon: "success"});
                                        $scope.isUploadingAvatar = false;
                                    }, err => {
                                        cc.notify({message: err.data, icon: "warning"});
                                        $scope.isUploadingAvatar = false;
                                    });
                            }

                            $scope.avatarUploader.onAfterAddingFile = function (item) {
                                item.headers['Datadocs-API-Arg'] = JSON.stringify({fileSize: item.file.size});
                                $scope.avatarUploader.uploadAll();
                                $scope.isUploadingAvatar = true;
                            };

                            $scope.avatarUploader.onSuccessItem = function (item, {url}) {
                                changeAvatar(url);
                            };
                        }
                    }
                }
            }
        }]);
});

define('modules/app/directives/uib-modal-window',['./module', 'jquery'], function (directives, $) {
    'use strict';
    directives.directive('uibModalWindow', [function () {
        return {
            restrict: 'EA',
            link: function($scope, $element) {
                if(!$element.hasClass('popup-window')) {
                    $element.find('.modal-content').draggable({
                        handle: '.modal-header',
                        containment: 'body',
                        stop: function() {
                            // fix transient background if height change after drag
                            this.style.height = "auto";
                        }
                    });
                }
            }
        }
    }]);
});

